import { Component, OnInit, ViewChild, ElementRef} from '@angular/core';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { GetService } from '../get.service';
import { ChatService } from '../home/chat/chat.service';

@Component({
  selector: 'app-chatroom',
  templateUrl: './chatroom.component.html',
  styleUrls: ['./chatroom.component.scss']
})


  export class ChatroomComponent implements OnInit{
    @ViewChild('messagecontainer') mContainer:ElementRef;
    userId:any;
    public phoneId: string;
    public messageText: string;
    public messageArray: { user: string, message: string, userName:string}[] = [];
    private storageArray = [];
    public selectedUser;
    public userList:any;
    currentUser:any;
    currentPhone:any;
    currentName:any;
    roomId:any;
    allData:any;
    roomsList:any;
    value:false;
    roomUserList:any;
    roomUser:any;
    roomAdmin:any;
    selectedroom:any;
    signUserList:any;
    blockList:any;
    blockedUser:any;
    muteList:any;
    mutedUsers:any;
    mutedUser:any;
    blockedSender:any;
    muteUser:any;

     
    constructor(
      private modalService: NgbModal,
      private chatService: ChatService,
      public get:GetService,
      public router:Router
    ) {
    }
  
    ngOnInit(): void { 
        this.get.getdata().subscribe((data)=>{
        this.allData=JSON.parse(JSON.stringify(data));
        this.userList=this.allData.uList;
        this.roomsList=this.allData.rList;
        this.roomUserList=this.allData.chatUserList;
        this.signUserList=this.allData.signList;
        this.userId=localStorage.getItem('userId');
        this.roomId=localStorage.getItem('currentRoom');
        this.currentUser=this.userList.filter((p: any)=>p.phone===this.userId);
        this.userList=this.userList.filter((p: any)=>p.phone!==this.userId);
        this.roomUser=this.roomUserList.filter((p: any)=>p.selectedRoom===this.roomId);
        this.blockList=this.roomUser.filter((p: any)=>p.block===true);
        this.mutedUsers=this.roomUser.filter((p: any)=>p.mute===true);
        this.roomAdmin=this.roomsList.filter((p: any)=>p.room===this.roomId);
        this.currentPhone=this.currentUser[0].phone;
        this.currentName=this.currentUser[0].name;
      });
       this.chatService.getMessage()
        .subscribe((data: { user: string, message: string, userName:string}) => {
          this.mutedUser=this.mutedUsers.filter((p: any)=>p.selectedPhone===data.user);
          if(this.roomUser!=''||this.userId==data.user){
            this.messageArray.push(data);
            this.messageArray.reverse();
          }
          
          if (this.userId) {
            setTimeout(() => {
              this.storageArray = this.chatService.getStorage();
              const storeIndex = this.storageArray
                .findIndex((storage) => storage.phone === this.userId);
              // this.messageArray = this.storageArray[storeIndex].chats;
            }, 500);
          }
        });
    }
  ngAfterViewInit(){
    this.mContainer.nativeElement.scrollTop=
    this.mContainer.nativeElement.scrollHeight;
  }
    logout(){
      localStorage.clear();
      this.router.navigate([''])
     }
     
    selectUserHandler(phone: string): void {
      this.selectedUser = this.userList.find(user => user.phone === phone);
      this.phoneId = this.selectedUser.phone;
      localStorage.setItem('selectedName',this.selectedUser.name);
      localStorage.setItem('selectedPhone',this.selectedUser.phone);
      this.selectedroom=this.roomUserList.find(user => user.selectedPhone === this.phoneId);
      if(this.roomAdmin[0].userId==this.userId){
        this.router.navigate(['/edit']);
      }
      this.join(this.currentUser, this.userId);
    }
  
    join(username: string, userId: string): void {
      this.chatService.joinRoom({user: username, userId: userId});
    }

    deleteRoom(){
      alert('You are going to delete this chatroom');
      this.chatService.deleteRoom({roomId:this.roomId});
      this.router.navigate(['home'])
    }

    
  
    sendMessage(): void {
      this.blockedSender=this.blockList.find(user => user.selectedPhone === this.userId);
      if(this.blockedSender){console.log('no')
        alert('You are blocked by admin');
        this.messageText='';
      }
      else{
        this.chatService.sendRoomMessage({
          user: this.userId, 
          toUser:this.phoneId,
          message: this.messageText,
          userName:this.currentName
        });
        this.messageText="";
      }
      
  
      this.storageArray = this.chatService.getStorage();
      const storeIndex = this.storageArray
        .findIndex((storage) => storage.phone === this.userId);
  
      if (storeIndex > -1) {
        this.storageArray[storeIndex].chats.push({
          user: this.currentUser,
          message: this.messageText
        });
      } else {
        const updateStorage = {
          roomId: this.userId,
          chats: [{
            user: this.currentUser,
            message: this.messageText
          }]
        }
  
        this.storageArray.push(updateStorage);
      }
      this.chatService.setStorage(this.storageArray);
      this.messageText = '';
      
    }
    
  }
  
