import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { io, Socket } from 'socket.io-client';

@Injectable({
  providedIn: 'root'
})
export class ChatService {
  userId:any;
server_address:string='api';
  private socket: Socket;
  private url = `${this.server_address}`; // your server local path

  constructor() {
    this.socket = io(this.url, {transports: ['websocket', 'polling', 'flashsocket']});
  }

  joinRoom(data): void {
    this.socket.emit('join', data);
  }

  sendMessage(data): void {
    this.socket.emit('message', data);
  }
  sendRoomMessage(data): void {
    this.socket.emit('room message', data);
  }
  select(data):void{
    this.socket.emit('selection', data);
  }
  deselect(data):void{
    this.socket.emit('deselection', data);
  }
  block(data):void{ 
    this.socket.emit('block',data);
  }
  unblock(data){
    this.socket.emit('unblock',data);
  }
  mute(data){
    this.socket.emit('mute',data);
  }
  unmute(data){console.log(data)
    this.socket.emit('unmute',data);
  }
  deleteUser(data){
    this.socket.emit('deleteUser',data);
  }
  deleteRoom(data){
    this.socket.emit('deleteRoom',data);
  }
  
 
  getMessage(): Observable<any> {
    this.userId=localStorage.getItem('userId');
    return new Observable<{user: string, message: string, toUser:string, mute:boolean}>(observer => {
      this.socket.on('new message', (data) => {
        observer.next(data); 
      });
      this.socket.on('new room message', (data) => {
        observer.next(data); 
      });
      this.socket.on('already', (data) => {
        observer.next(data);
        if(data.blockedno==this.userId){
          window.location.reload();
        }
      });

      return () => {
        this.socket.disconnect();
      }
    });
  }

  getStorage() {
    const storage: string = localStorage.getItem('chats');
    return storage ? JSON.parse(storage) : [];
  }

  setStorage(data) {
    localStorage.setItem('chats', JSON.stringify(data));
  }

}

